import QueueAnim from "rc-queue-anim";
import React from "react";
import style from "./index.scss";

class About extends React.Component {
  public render() {
    return (
      <div className={style.container}>
        <QueueAnim
          type="right"
          ease="easeInOutQuart"
        >
          <section key="AboutMe">
            <h1 className={style.title}></h1>
            <div className={style.content}>
              <div>
                
              </div>
              <div>
                2017
              </div>
              <div>
                
              </div>
              <div></div>
              <div></div>
              {/* <div>
              <ol>
                  <li>
                    Vue(vue/vue-router/vuex/elementUI)/React(react/react-router/redux/antd)
                  </li>
                  <li>
                    ElectronAPPES6
                  </li>
                  <li>Webpackparcel</li>
                  <li>Git</li>
                  <li></li>
                </ol>
              </div> */}
            </div>
          </section>

          <section key="ContactMe">
            <h1 className={style.title}></h1>
            <div className={style.content}>
              <p>
                <i className="iconfont icon-qq" />
                <span>122760248</span>
              </p>
              <p>
                <i className="iconfont icon-weixin" />
                <span>unbolibobo</span>
              </p>
              <p>
                <i className="iconfont icon-github" />
                <span>https://github.com/Bendjen</span>
              </p>
              <p>
                <i className="iconfont icon-email" />
                <span>unbolibobo@foxmail.com</span>
              </p>
            </div>
          </section>

          <section key="aboutSite">
            <h1 className={style.title}></h1>
            <div className={style.content}>
              <div>
                
                <a target="_blank" href="https://bendjen.github.io/parcel-old/">
                  https://bendjen.github.io/parcel-old/
                </a>
              </div>
              <div> reactwebpacktypescriptantd</div>

              <div>
                
                <ol>
                  <li>
                    reduxdemo
                  </li>
                  <li>
                    demohomedetaimarkaboutdemodetail
                  </li>
                  <li>
                    typeScript
                  </li>
                </ol>
              </div>

              <div>
                
                
              </div>
            </div>
          </section>
        </QueueAnim>
      </div>
    );
  }
}

export default About;



